<template>
<v-app>
  <v-toolbar fixed flat class="mainToolbar">
    <v-container class="homeContainer">
      <v-layout class="rowz">
        <v-flex xs6>
          <router-link class="titleMain pl-3" to="/">
            Milan Petrovic
          </router-link>
        </v-flex>
        <v-flex xs6 class="text-xs-right">
          <router-link class="contact pr-3" to="/contact">
            Contact
          </router-link>
        </v-flex>
      </v-layout>
    </v-container>
  </v-toolbar>
  <v-container class="homeContainer margtop" grid-list-lg>
    <v-layout row wrap class="text-xs-center">
      <v-flex xs12 md6 v-for="album in albums">
        <router-link :to="album.link">
          <div id="album">
            <img :src="album.imgURL"  @click="moveTop">
          </div>
        </router-link>
        <h5 class="text-xs-center">{{album.text}}</h5>
      </v-flex>
    </v-layout>
  </v-container>
</v-app>
</template>
<script>
export default {
  data() {
    return {
      albums: [{
          imgURL: '../static/1.png',
          text: 'album_1',
          link: '/album_1'
        },
        {
          imgURL: './static/1.jpg',
          text: 'album_2',
          link: '/album_2'
        },
        {
          imgURL: './static/2.jpg',
          text: 'album_3',
          link: '/album_3'
        },
        {
          imgURL: './static/3.jpg',
          text: 'album_4',
          link: '/album_4'
        },
        {
          imgURL: './static/4.png',
          text: 'album_5',
          link: '/album_5'
        },
        {
          imgURL: './static/5.jpg',
          text: 'album_6',
          link: '/album_6'
        }
      ]
    }
  },
  methods:{
    moveTop(){
      window.scrollTo(0, 0);
    }
  }
}
</script>
<style>

.application.theme--light{
  background: #fcfcfc !important;
}
img {
  max-width: 100%;
  height: auto;
}

.mainContainer {
  max-width: 1000px!important;
  padding: 0;
}
.homeContainer{
  max-width: 880px!important;
  padding: 0;
}

.mainToolbar {
  height: 80px;
  background-color: #fcfcfc !important;
}

.rowz {
  line-height: 80px !important;
}


.titleMain,
.contact {
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.margtop {
  margin-top: 110px !important;
}

.fade-enter-active {
  transition: .3s ease-in-out;
}

.fade-leave-to,
.fade-enter {
  transform: translatex(-5%);
  filter: blur(10px);
}
</style>



